/*=============== VARIABLES CSS ===============*/
:root {
  --header-height: 3.5rem;

  /*========== Colors ==========*/
  --hue: 169;
  --first-color: hsl(var(--hue), 90%, 40%);
  --first-color-alt: hsl(var(--hue), 92%, 46%);
  --first-color-light: hsl(var(--hue), 40%, 80%);
  --title-color: hsl(var(--hue), 24%, 16%);
  --text-color: hsl(var(--hue), 8%, 45%);
  --text-color-light: hsl(var(--hue), 8%, 60%);
  --white-color: hsl(0, 0%, 100%);
  --body-color: hsl(0, 0%, 100%);
  --container-color: hsl(0, 0%, 100%);
  --shadow-color: hsla(var(--hue), 90%, 30%, 0.1);

  /*========== Font and typography ==========*/
  /*.5rem = 8px | 1rem = 16px ...*/
  --body-font: "Syne", sans-serif;
  --biggest-font-size: 2rem;
  --h1-font-size: 1.5rem;
  --h2-font-size: 1.25rem;
  --h3-font-size: 1rem;
  --normal-font-size: 0.938rem;
  --small-font-size: 0.813rem;
  --smaller-font-size: 0.75rem;

  /*========== Font weight ==========*/
  --font-regular: 400;
  --font-medium: 500;
  --font-semi-bold: 600;

  /*========== z index ==========*/
  --z-tooltip: 10;
  --z-fixed: 100;
  --z-modal: 1000;
}

/*========== Responsive typography ==========*/
@media screen and (min-width: 1150px) {
  :root {
    --biggest-font-size: 4.5rem;
    --h1-font-size: 2.25rem;
    --h2-font-size: 1.5rem;
    --h3-font-size: 1.25rem;
    --normal-font-size: 1.125rem;
    --small-font-size: 0.875rem;
    --smaller-font-size: 0.813rem;
  }
}

/*=============== BASE ===============*/
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html {
  scroll-behavior: smooth;
}

body,
input,
textarea,
button {
  font-family: var(--body-font) !important;
  font-size: var(--normal-font-size) !important;
}

body {
  background-color: var(--body-color) !important;
  color: var(--text-color) !important;
  transition: background-color 0.4s !important;
  text-align: justify !important;
}

input,
button,
textarea {
  border: none;
  outline: none;
}

h1,
h2,
h3,
h4,
p {
  color: var(--title-color);
  font-weight: var(--font-semi-bold);
}

ul {
  list-style: none;
}

a {
  text-decoration: none !important;
}

img {
  display: block;
  max-width: 100%;
  height: 100%;
}

/*=============== THEME ===============*/
.nav__actions {
  display: flex;
  align-items: center;
  column-gap: 1rem;
}

.change-theme {
  color: var(--title-color);
  font-size: 1.25rem;
  cursor: pointer;
  transition: color 0.4s;
}

/*========== Variables Dark theme ==========*/
body.dark-theme {
  --title-color: hsl(var(--hue), 24%, 85%) !important;
  --text-color: hsl(var(--hue), 8%, 70%) !important;
  --body-color: hsl(var(--hue), 40%, 8%) !important;
  --container-color: hsl(var(--hue), 16%, 12%) !important;
  --shadow-color: hsla(var(--hue), 32%, 4%, 0.4) !important;
}

/*========== dark theme ==========*/
.dark-theme .bg-header {
  box-shadow: 0 12px 24px var(--shadow-color) !important;
}

.dark-theme .populer__modal {
  background-color: hsla(var(--hue), 90%, 8%, 0.2) !important;
}

.dark-theme::-webkit-scrollbar {
  background-color: hsl(var(--hue), 16%, 15%) !important;
}

.dark-theme::-webkit-scrollbar-thumb {
  background-color: hsl(var(--hue), 16%, 25%) !important;
}

.dark-theme::-webkit-scrollbar-thumb:hover {
  background-color: hsl(var(--hue), 16%, 35%) !important;
}

/*=============== REUSABLE CSS CLASSES ===============*/

.grid {
  display: grid;
  gap: 1.5rem;
}

.section {
  padding-block: 5rem 1rem;
}
.section__title {
  font-size: var(--h1-font-size);
  text-align: center;
  margin-bottom: 2rem;
}

.perfil__img {
  width: 180px;
}

.main {
  overflow: hidden;
}

/*=============== HEADER & NAV ===============*/
.header {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  background-color: transparent;
  z-index: var(--z-fixed);
  transition: background-color 0.4s, box-shadow 4s;
}

.nav {
  position: relative;
  height: var(--header-height);
  display: flex;
  justify-content: space-between;
  align-items: center;
}

nav a {
  margin-left: 10px;
}

.nav__logo {
  color: var(--title-color);
  font-weight: bold;
  transition: color 0.4s;
}

.nav__logo.beranda {
  color: var(--white-color);
  font-weight: bold;
  transition: color 0.4s;
}

.nav__toggle,
.nav__close {
  display: inline-flex;
  font-size: 1.25rem;
  cursor: pointer;
}

.nav__toggle {
  color: var(--title-color);
  transition: color 0.4s;
}

.nav__close {
  color: var(--title-color);
}

/* Navigation for mobile devices */
@media screen and (max-width: 1150px) {
  .nav__menu {
    position: fixed;
    top: -100%;
    left: 0;
    /* background-color: var(--body-color); */
    backdrop-filter: blur(16px);
    -webkit-backdrop-filter: blur(16px);
    width: 100%;
    box-shadow: 0 12px 24px var(--shadow-color);
    padding-block: 5rem 4rem;
    transition: top 0.4s;
  }
}

.nav__list {
  display: flex;
  flex-direction: column;
  row-gap: 2.5rem;
  text-align: center;
}

.nav__link {
  color: var(--title-color);
  font-weight: var(--font-semi-bold);
  transition: color 0.4s;
}

.nav__link:hover {
  color: var(--first-color);
}

.nav__close {
  position: absolute;
  top: 1.15rem;
  right: 1.5rem;
}

/* Show menu */
.show-menu {
  top: 0;
}

/* Add blur header */
.blur-header::after {
  content: "";
  position: absolute;
  width: 1000%;
  height: 100%;
  /* background-color: hsla(0, 0%, 6%, .2); */
  backdrop-filter: blur(16px);
  -webkit-backdrop-filter: blur(16px);
  top: 0;
  left: 0;
  z-index: -1;
}

.bg-header {
  box-shadow: 0 2px 16px var(--shadow-color);
}

.bg-header :is(.nav__logo, .nav__toggle, .change-theme) {
  color: var(--title-color);
}

.header-pages::after {
  content: "";
  position: absolute;
  width: 1000%;
  height: 100%;
  /* background-color: hsla(0, 0%, 6%, .2); */
  backdrop-filter: blur(16px);
  -webkit-backdrop-filter: blur(16px);
  top: 0;
  left: 0;
  z-index: -1;
}

.header-pages :is(.nav__logo, .nav__toggle, .change-theme) {
  color: var(--title-color);
}

/*=============== BUTTON ===============*/
.button {
  display: inline-block;
  background-color: var(--first-color);
  color: var(--white-color);
  font-weight: var(--font-semi-bold);
  padding: 1rem 2rem;
  border-radius: 25px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: background-color 0.4s, box-shadow 0.4s;
}

.button-view {
  display: inline-block;
  background-color: var(--first-color);
  color: var(--white-color);
  font-weight: var(--font-semi-bold);
  padding: 0.5rem 1rem;
  border-radius: 25px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: background-color 0.4s, box-shadow 0.4s;
}

.button:hover,
.button-view:hover {
  background-color: var(--first-color-alt);
  box-shadow: 0 8px 24px hsla(var(--hue), 90%, 30%, 0.3);
  color: var(--white-color);
}

/*=============== HOME ===============*/
.home {
  position: relative;
  border-bottom: 8px solid var(--first-color);
}

.home__rectangle {
  width: 100%;
  height: 220px;
  background-color: var(--first-color);
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
}

.home__container {
  padding-block: 1rem 3rem;
}

.home__perfil {
  justify-self: center;
}

.home__content {
  row-gap: 3rem;
}

.home__data {
  row-gap: 0.75rem;
  text-align: center;
}

.home__name {
  font-size: var(--biggest-font-size);
}

.home__profession {
  font-size: var(--h2-font-size);
}

.home__social {
  display: flex;
  justify-content: center;
  column-gap: 1rem;
}

.home__social-link {
  font-size: 1.5rem;
  color: var(--title-color);
  transition: color 0.4s;
}

.home__social-link:hover {
  color: var(--first-color);
  transform: translateY(-0.25rem);
  transition: transform 0.4s;
}

.home__button {
  justify-self: center;
}

/*=============== POPULER ===============*/
.populer__container {
  row-gap: 2rem;
}

.populer__card {
  position: relative;
  overflow: hidden;
  background-color: var(--container-color);
  box-shadow: 0 12px 24px var(--shadow-color);
  transition: background-color 0.4s;
  border-radius: 20px;
}

.populer__modal {
  position: absolute;
  width: 100%;
  height: 100%;
  bottom: -100%;
  left: 0;
  background: linear-gradient(
    180deg,
    hsla(var(--hue), 24%, 40%, 0.3) 0%,
    hsla(var(--hue), 24%, 4%, 1) 95%
  );
  display: grid;
  align-items: flex-end;
  padding: 1.5rem 1.25rem;
  transition: 0.3s;
}

.populer__title {
  font-size: var(--h2-font-size);
  margin-bottom: 0.75rem;
}

.populer__description {
  margin-bottom: 3rem;
}

.populer__subtitle,
.populer__title {
  color: #fff;
}

.populer__subtitle {
  font-size: var(--smaller-font-size);
}

.populer__button {
  cursor: pointer;
}

.populer__card:hover .populer__modal {
  bottom: 0;
}

/* Active modal */
.active-modal {
  opacity: 1;
  visibility: visible;
}

/*=============== EXPLORE ===============*/
.explore__container {
  row-gap: 2rem;
  min-height: 50vh;
}

.explore__link {
  display: inline-block;
  position: relative;
  overflow: hidden;
  margin-bottom: 1rem;
  border-radius: 20px;
}

.explore__img {
  /* filter: grayscale(1); */
  transition: filter 0.5s, transform 0.5s;
  border-radius: 20px;
  min-height: 260px;
}

.explore__icon {
  position: absolute;
  top: 1.25rem;
  right: 1.25rem;
  font-size: 2rem;
  color: var(--first-color);
  transition: transform 0.4s;
}

.explore__link:hover .explore__img {
  transform: scale(1.1);
  filter: none;
  transition: transform 0.5s cubic-bezier(0.23, 1, 0.32, 1);
}

@keyframes bubble {
  0%,
  100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-5px);
  }
}

.explore__link:hover {
  animation: bubble 0.7s cubic-bezier(0.47, 0, 0.745, 0.715) infinite;
}

.explore__link:hover .explore__icon {
  transform: translate(0.5rem, -0.5rem) rotate(-30deg);
}

.explore__title {
  font-size: var(--h2-font-size);
  margin-bottom: 0.25rem;
}

.explore__page {
  padding-bottom: 4rem;
}

/*=============== CONTACT ===============*/
.contact__container {
  padding-bottom: 4rem;
}

.contact__button {
  justify-self: center;
}

.contact__page {
  padding-block: 1rem 4rem;
}

.contact__form {
  position: relative;
}

.contact__box {
  position: relative;
  width: 100%;
  height: 58px;
}

.contact__input {
  width: 100%;
  height: 100%;
  background-color: var(--body-color);
  border: 3px solid var(--text-color-light);
  padding: 0.5rem 1.5rem;
  color: var(--title-color);
  font-weight: var(--font-semi-bold);
  transition: border-color 0.4s, background-color 0.4s;
}

.contact__input:focus {
  border-color: var(--title-color);
}

.contact__input:-webkit-autofill {
  transition: background-color 6000s, color 6000s;
}

.contact__label {
  position: absolute;
  top: -16px;
  left: 16px;
  font-size: var(--small-font-size);
  font-weight: var(--font-semi-bold);
  color: var(--title-color);
  background-color: var(--body-color);
  padding: 8px;
  transition: background-color 0.4s;
}

.contact__area {
  height: 10rem;
}

.contact__area textarea {
  resize: none;
  padding-top: 1.25rem;
}

.contact__send {
  padding-block: 1.25rem;
  cursor: pointer;
}

.contact__message {
  position: absolute;
  bottom: 3rem;
  left: 0;
  font-size: var(--small-font-size);
  font-weight: var(--font-semi-bold);
  color: var(--title-color);
}

/*=============== FOOTER ===============*/
.footer {
  background-color: var(--first-color);
}

.footer__container {
  padding-block: 4rem 2rem;
  text-align: center;
  row-gap: 5rem;
}

.footer__content {
  row-gap: 2rem;
}

.footer__content a,
.footer__copy {
  color: var(--white-color);
}

.footer__logo {
  font-size: var(--h1-font-size);
  font-weight: var(--font-semi-bold);
  justify-self: center;
}

.footer__links,
.footer__social {
  display: flex;
  justify-content: center;
}

.footer__links {
  flex-wrap: wrap;
  gap: 2rem;
}

.footer__link {
  font-weight: var(--font-semi-bold);
}

.footer__logo:hover,
.footer__link:hover {
  text-decoration: underline;
}

.footer__social {
  column-gap: 1rem;
}

.footer__social-link {
  font-size: 1.5rem;
  transition: transform 0.4s;
}

.footer__social-link:hover {
  transform: translateY(-0.25rem);
}

.footer__copy {
  font-size: var(--small-font-size);
  font-weight: var(--font-medium);
}

/*=============== SCROLL BAR ===============*/
::-webkit-scrollbar {
  width: 0.6rem;
  background-color: hsl(var(--hue), 20%, 85%);
}

::-webkit-scrollbar-thumb {
  background-color: hsl(var(--hue), 20%, 65%);
}

::-webkit-scrollbar-thumb:hover {
  background-color: hsl(var(--hue), 20%, 55%);
}

/*=============== SCROLL UP ===============*/
.scrollup {
  position: fixed;
  right: 1rem;
  bottom: -50%;
  background-color: var(--container-color);
  box-shadow: 0 12px 24px var(--shadow-color);
  display: inline-flex;
  padding: 6px;
  font-size: 1.25rem;
  color: var(--title-color);
  z-index: var(--z-tooltip);
  transition: bottom 0.4s, transform 0.4s, background-color 0.4s;
}

.scrollup:hover {
  transform: translateY(-0.5rem);
}

/* Show Scroll Up */
.show-scroll {
  bottom: 3rem;
}

/* detail */

.hero-footer {
  width: 100%;
  height: 50px;
  background-color: var(--first-color);
}

.hero-img-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 60vh;
  overflow: hidden;
}

.img-fluidd {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.map {
  width: 100%;
  height: 200px;
  background-color: var(--first-color);
}

.frame-map {
  height: 200px;
}

.bg-costum {
  background-color: var(--first-color);
}

.img-container {
  position: relative;
  width: 100%;
  height: 250px;
  overflow: hidden;
}

.img-container img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.img-container h4 {
  position: absolute;
  bottom: 2rem;
  left: 2rem;
}

.video-container {
  position: relative;
  padding-bottom: 56.25%; /* 16:9 aspect ratio */
  height: 0;
  overflow: hidden;
  max-width: 100%;
  background: #0d1c34;
  background-color: #0d1c34;
}
.video-container iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.other {
  margin-top: 100px;
}

.scrollup {
  cursor: pointer;
}

.form-control:focus {
  border-color: var(--first-color);
  box-shadow: 0 0 0 .25rem rgba(10, 194, 160, 0.5);
}
