,UAAU;AACZ;;AAEA;EACE,kBAAkB;EAClB,sBAAsB,EAAE,sBAAsB;EAC9C,SAAS;EACT,gBAAgB;EAChB,eAAe;EACf,mBAAmB;EACnB,yBAAyB;AAC3B;AACA;EACE,kBAAkB;EAClB,MAAM;EACN,OAAO;EACP,WAAW;EACX,YAAY;AACd;;AAEA;EACE,iBAAiB;AACnB;;AAEA;EACE,eAAe;AACjB;;AAEA;EACE,gCAAgC;EAChC,gDAAgD;AAClD","sourcesContent":["/*=============== GOOGLE FONTS ===============*/\r\n@import url(\"https://fonts.googleapis.com/css2?family=Syne:wght@400;500;600&display=swap\");\r\n\r\n/*=============== VARIABLES CSS ===============*/\r\n:root {\r\n  --header-height: 3.5rem;\r\n\r\n  /*========== Colors ==========*/\r\n  --hue: 169;\r\n  --first-color: hsl(var(--hue), 90%, 40%);\r\n  --first-color-alt: hsl(var(--hue), 92%, 46%);\r\n  --first-color-light: hsl(var(--hue), 40%, 80%);\r\n  --title-color: hsl(var(--hue), 24%, 16%);\r\n  --text-color: hsl(var(--hue), 8%, 45%);\r\n  --text-color-light: hsl(var(--hue), 8%, 60%);\r\n  --white-color: hsl(0, 0%, 100%);\r\n  --body-color: hsl(0, 0%, 100%);\r\n  --container-color: hsl(0, 0%, 100%);\r\n  --shadow-color: hsla(var(--hue), 90%, 30%, 0.1);\r\n\r\n  /*========== Font and typography ==========*/\r\n  /*.5rem = 8px | 1rem = 16px ...*/\r\n  --body-font: \"Syne\", sans-serif;\r\n  --biggest-font-size: 2rem;\r\n  --h1-font-size: 1.5rem;\r\n  --h2-font-size: 1.25rem;\r\n  --h3-font-size: 1rem;\r\n  --normal-font-size: 0.938rem;\r\n  --small-font-size: 0.813rem;\r\n  --smaller-font-size: 0.75rem;\r\n\r\n  /*========== Font weight ==========*/\r\n  --font-regular: 400;\r\n  --font-medium: 500;\r\n  --font-semi-bold: 600;\r\n\r\n  /*========== z index ==========*/\r\n  --z-tooltip: 10;\r\n  --z-fixed: 100;\r\n  --z-modal: 1000;\r\n}\r\n\r\n/*========== Responsive typography ==========*/\r\n@media screen and (min-width: 1150px) {\r\n  :root {\r\n    --biggest-font-size: 4.5rem;\r\n    --h1-font-size: 2.25rem;\r\n    --h2-font-size: 1.5rem;\r\n    --h3-font-size: 1.25rem;\r\n    --normal-font-size: 1.125rem;\r\n    --small-font-size: 0.875rem;\r\n    --smaller-font-size: 0.813rem;\r\n  }\r\n}\r\n\r\n/*=============== BASE ===============*/\r\n* {\r\n  box-sizing: border-box;\r\n  padding: 0;\r\n  margin: 0;\r\n}\r\n\r\nhtml {\r\n  scroll-behavior: smooth;\r\n}\r\n\r\nbody,\r\ninput,\r\ntextarea,\r\nbutton {\r\n  font-family: var(--body-font) !important;\r\n  font-size: var(--normal-font-size) !important;\r\n}\r\n\r\nbody {\r\n  background-color: var(--body-color) !important;\r\n  color: var(--text-color) !important;\r\n  transition: background-color 0.4s !important;\r\n  text-align: justify !important;\r\n}\r\n\r\ninput,\r\nbutton,\r\ntextarea {\r\n  border: none;\r\n  outline: none;\r\n}\r\n\r\nh1,\r\nh2,\r\nh3,\r\nh4,\r\np {\r\n  color: var(--title-color);\r\n  font-weight: var(--font-semi-bold);\r\n}\r\n\r\nul {\r\n  list-style: none;\r\n}\r\n\r\na {\r\n  text-decoration: none !important;\r\n}\r\n\r\nimg {\r\n  display: block;\r\n  max-width: 100%;\r\n  height: 100%;\r\n}\r\n\r\n/*=============== THEME ===============*/\r\n.nav__actions {\r\n  display: flex;\r\n  align-items: center;\r\n  column-gap: 1rem;\r\n}\r\n\r\n.change-theme {\r\n  color: var(--title-color);\r\n  font-size: 1.25rem;\r\n  cursor: pointer;\r\n  transition: color 0.4s;\r\n}\r\n\r\n/*========== Variables Dark theme ==========*/\r\nbody.dark-theme {\r\n  --title-color: hsl(var(--hue), 24%, 85%) !important;\r\n  --text-color: hsl(var(--hue), 8%, 70%) !important;\r\n  --body-color: hsl(var(--hue), 40%, 8%) !important;\r\n  --container-color: hsl(var(--hue), 16%, 12%) !important;\r\n  --shadow-color: hsla(var(--hue), 32%, 4%, 0.4) !important;\r\n}\r\n\r\n/*========== dark theme ==========*/\r\n.dark-theme .bg-header {\r\n  box-shadow: 0 12px 24px var(--shadow-color) !important;\r\n}\r\n\r\n.dark-theme .populer__modal {\r\n  background-color: hsla(var(--hue), 90%, 8%, 0.2) !important;\r\n}\r\n\r\n.dark-theme::-webkit-scrollbar {\r\n  background-color: hsl(var(--hue), 16%, 15%) !important;\r\n}\r\n\r\n.dark-theme::-webkit-scrollbar-thumb {\r\n  background-color: hsl(var(--hue), 16%, 25%) !important;\r\n}\r\n\r\n.dark-theme::-webkit-scrollbar-thumb:hover {\r\n  background-color: hsl(var(--hue), 16%, 35%) !important;\r\n}\r\n\r\n/*=============== REUSABLE CSS CLASSES ===============*/\r\n\r\n.grid {\r\n  display: grid;\r\n  gap: 1.5rem;\r\n}\r\n\r\n.section {\r\n  padding-block: 5rem 1rem;\r\n}\r\n.section__title {\r\n  font-size: var(--h1-font-size);\r\n  text-align: center;\r\n  margin-bottom: 2rem;\r\n}\r\n\r\n.perfil__img {\r\n  width: 180px;\r\n}\r\n\r\n.main {\r\n  overflow: hidden;\r\n}\r\n\r\n/*=============== HEADER & NAV ===============*/\r\n.header {\r\n  position: fixed;\r\n  width: 100%;\r\n  top: 0;\r\n  left: 0;\r\n  background-color: transparent;\r\n  z-index: var(--z-fixed);\r\n  transition: background-color 0.4s, box-shadow 4s;\r\n}\r\n\r\n.nav {\r\n  position: relative;\r\n  height: var(--header-height);\r\n  display: flex;\r\n  justify-content: space-between;\r\n  align-items: center;\r\n}\r\n\r\nnav a {\r\n  margin-left: 10px;\r\n}\r\n\r\n.nav__logo {\r\n  color: var(--title-color);\r\n  font-weight: bold;\r\n  transition: color 0.4s;\r\n}\r\n\r\n.nav__logo.beranda {\r\n  color: var(--white-color);\r\n  font-weight: bold;\r\n  transition: color 0.4s;\r\n}\r\n\r\n.nav__toggle,\r\n.nav__close {\r\n  display: inline-flex;\r\n  font-size: 1.25rem;\r\n  cursor: pointer;\r\n}\r\n\r\n.nav__toggle {\r\n  color: var(--title-color);\r\n  transition: color 0.4s;\r\n}\r\n\r\n.nav__close {\r\n  color: var(--title-color);\r\n}\r\n\r\n/* Navigation for mobile devices */\r\n@media screen and (max-width: 1150px) {\r\n  .nav__menu {\r\n    position: fixed;\r\n    top: -100%;\r\n    left: 0;\r\n    /* background-color: var(--body-color); */\r\n    backdrop-filter: blur(16px);\r\n    -webkit-backdrop-filter: blur(16px);\r\n    width: 100%;\r\n    box-shadow: 0 12px 24px var(--shadow-color);\r\n    padding-block: 5rem 4rem;\r\n    transition: top 0.4s;\r\n  }\r\n}\r\n\r\n.nav__list {\r\n  display: flex;\r\n  flex-direction: column;\r\n  row-gap: 2.5rem;\r\n  text-align: center;\r\n}\r\n\r\n.nav__link {\r\n  color: var(--title-color);\r\n  font-weight: var(--font-semi-bold);\r\n  transition: color 0.4s;\r\n}\r\n\r\n.nav__link:hover {\r\n  color: var(--first-color);\r\n}\r\n\r\n.nav__close {\r\n  position: absolute;\r\n  top: 1.15rem;\r\n  right: 1.5rem;\r\n}\r\n\r\n/* Show menu */\r\n.show-menu {\r\n  top: 0;\r\n}\r\n\r\n/* Add blur header */\r\n.blur-header::after {\r\n  content: \"\";\r\n  position: absolute;\r\n  width: 1000%;\r\n  height: 100%;\r\n  /* background-color: hsla(0, 0%, 6%, .2); */\r\n  backdrop-filter: blur(16px);\r\n  -webkit-backdrop-filter: blur(16px);\r\n  top: 0;\r\n  left: 0;\r\n  z-index: -1;\r\n}\r\n\r\n.bg-header {\r\n  box-shadow: 0 2px 16px var(--shadow-color);\r\n}\r\n\r\n.bg-header :is(.nav__logo, .nav__toggle, .change-theme) {\r\n  color: var(--title-color);\r\n}\r\n\r\n.header-pages::after {\r\n  content: \"\";\r\n  position: absolute;\r\n  width: 1000%;\r\n  height: 100%;\r\n  /* background-color: hsla(0, 0%, 6%, .2); */\r\n  backdrop-filter: blur(16px);\r\n  -webkit-backdrop-filter: blur(16px);\r\n  top: 0;\r\n  left: 0;\r\n  z-index: -1;\r\n}\r\n\r\n.header-pages :is(.nav__logo, .nav__toggle, .change-theme) {\r\n  color: var(--title-color);\r\n}\r\n\r\n/*=============== BUTTON ===============*/\r\n.button {\r\n  display: inline-block;\r\n  background-color: var(--first-color);\r\n  color: var(--white-color);\r\n  font-weight: var(--font-semi-bold);\r\n  padding: 1rem 2rem;\r\n  border-radius: 25px;\r\n  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);\r\n  transition: background-color 0.4s, box-shadow 0.4s;\r\n}\r\n\r\n.button-view {\r\n  display: inline-block;\r\n  background-color: var(--first-color);\r\n  color: var(--white-color);\r\n  font-weight: var(--font-semi-bold);\r\n  padding: 0.5rem 1rem;\r\n  border-radius: 25px;\r\n  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);\r\n  transition: background-color 0.4s, box-shadow 0.4s;\r\n}\r\n\r\n.button:hover,\r\n.button-view: