em 4rem;\r\n}\r\n\r\n.contact__form {\r\n  position: relative;\r\n}\r\n\r\n.contact__box {\r\n  position: relative;\r\n  width: 100%;\r\n  height: 58px;\r\n}\r\n\r\n.contact__input {\r\n  width: 100%;\r\n  height: 100%;\r\n  background-color: var(--body-color);\r\n  border: 3px solid var(--text-color-light);\r\n  padding: 0.5rem 1.5rem;\r\n  color: var(--title-color);\r\n  font-weight: var(--font-semi-bold);\r\n  transition: border-color 0.4s, background-color 0.4s;\r\n}\r\n\r\n.contact__input:focus {\r\n  border-color: var(--title-color);\r\n}\r\n\r\n.contact__input:-webkit-autofill {\r\n  transition: background-color 6000s, color 6000s;\r\n}\r\n\r\n.contact__label {\r\n  position: absolute;\r\n  top: -16px;\r\n  left: 16px;\r\n  font-size: var(--small-font-size);\r\n  font-weight: var(--font-semi-bold);\r\n  color: var(--title-color);\r\n  background-color: var(--body-color);\r\n  padding: 8px;\r\n  transition: background-color 0.4s;\r\n}\r\n\r\n.contact__area {\r\n  height: 10rem;\r\n}\r\n\r\n.contact__area textarea {\r\n  resize: none;\r\n  padding-top: 1.25rem;\r\n}\r\n\r\n.contact__send {\r\n  padding-block: 1.25rem;\r\n  cursor: pointer;\r\n}\r\n\r\n.contact__message {\r\n  position: absolute;\r\n  bottom: 3rem;\r\n  left: 0;\r\n  font-size: var(--small-font-size);\r\n  font-weight: var(--font-semi-bold);\r\n  color: var(--title-color);\r\n}\r\n\r\n/*=============== FOOTER ===============*/\r\n.footer {\r\n  background-color: var(--first-color);\r\n}\r\n\r\n.footer__container {\r\n  padding-block: 4rem 2rem;\r\n  text-align: center;\r\n  row-gap: 5rem;\r\n}\r\n\r\n.footer__content {\r\n  row-gap: 2rem;\r\n}\r\n\r\n.footer__content a,\r\n.footer__copy {\r\n  color: var(--white-color);\r\n}\r\n\r\n.footer__logo {\r\n  font-size: var(--h1-font-size);\r\n  font-weight: var(--font-semi-bold);\r\n  justify-self: center;\r\n}\r\n\r\n.footer__links,\r\n.footer__social {\r\n  display: flex;\r\n  justify-content: center;\r\n}\r\n\r\n.footer__links {\r\n  flex-wrap: wrap;\r\n  gap: 2rem;\r\n}\r\n\r\n.footer__link {\r\n  font-weight: var(--font-semi-bold);\r\n}\r\n\r\n.footer__logo:hover,\r\n.footer__link:hover {\r\n  text-decoration: underline;\r\n}\r\n\r\n.footer__social {\r\n  column-gap: 1rem;\r\n}\r\n\r\n.footer__social-link {\r\n  font-size: 1.5rem;\r\n  transition: transform 0.4s;\r\n}\r\n\r\n.footer__social-link:hover {\r\n  transform: translateY(-0.25rem);\r\n}\r\n\r\n.footer__copy {\r\n  font-size: var(--small-font-size);\r\n  font-weight: var(--font-medium);\r\n}\r\n\r\n/*=============== SCROLL BAR ===============*/\r\n::-webkit-scrollbar {\r\n  width: 0.6rem;\r\n  background-color: hsl(var(--hue), 20%, 85%);\r\n}\r\n\r\n::-webkit-scrollbar-thumb {\r\n  background-color: hsl(var(--hue), 20%, 65%);\r\n}\r\n\r\n::-webkit-scrollbar-thumb:hover {\r\n  background-color: hsl(var(--hue), 20%, 55%);\r\n}\r\n\r\n/*=============== SCROLL UP ===============*/\r\n.scrollup {\r\n  position: fixed;\r\n  right: 1rem;\r\n  bottom: -50%;\r\n  background-color: var(--container-color);\r\n  box-shadow: 0 12px 24px var(--shadow-color);\r\n  display: inline-flex;\r\n  padding: 6px;\r\n  font-size: 1.25rem;\r\n  color: var(--title-color);\r\n  z-index: var(--z-tooltip);\r\n  transition: bottom 0.4s, transform 0.4s, background-color 0.4s;\r\n}\r\n\r\n.scrollup:hover {\r\n  transform: translateY(-0.5rem);\r\n}\r\n\r\n/* Show Scroll Up */\r\n.show-scroll {\r\n  bottom: 3rem;\r\n}\r\n\r\n/* detail */\r\n\r\n.hero-footer {\r\n  width: 100%;\r\n  height: 50px;\r\n  background-color: var(--first-color);\r\n}\r\n\r\n.hero-img-container {\r\n  display: flex;\r\n  justify-content: center;\r\n  align-items: center;\r\n  height: 60vh;\r\n  overflow: hidden;\r\n}\r\n\r\n.img-fluidd {\r\n  width: 100%;\r\n  height: 100%;\r\n  object-fit: cover;\r\n}\r\n\r\n.map {\r\n  width: 100%;\r\n  height: 200px;\r\n  background-color: var(--first-color);\r\n}\r\n\r\n.frame-map {\r\n  height: 200px;\r\n}\r\n\r\n.bg-costum {\r\n  background-color: var(--first-color);\r\